import { useNavigate } from "react-router-dom";
import { Row, Col, Card, Button, Typography } from "antd";

import { img_base_url } from "../../request/index"
const { Text } = Typography;

function TravelCard({ travelInfo }) {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate(`/travel/${travelInfo.id}`);
  };

  return (
    <>
      <Card
        hoverable
        cover={
          <div style={{ position: "relative", width: "100%" }}>
            <img
              alt="product"
              src={img_base_url + travelInfo.cover_image}
              style={{ width: "100%", aspectRatio: "16/9", objectFit: "cover" }}
            />
            {/* <div
              style={{
                position: "absolute",
                top: '0px', 
                left: '0px', 
                transform: "rotate(-45deg)",
                backgroundColor: "green",
              }}
            >
              <Text tt type="danger">
                热门
              </Text>
            </div> */}
          </div>
        }
        actions={[
          <div
            style={{
              display: "flex",
              justifyContent: "space-around",
              alignItems: "center",
              width: "100%",
              height: "1rem",
            }}
          >
            <div>
              {/* 显示价格的部分 */}
              <p style={{ fontSize: 18, fontWeight: "bold", color: "red" }}>
                ¥ {travelInfo.price}
              </p>
            </div>
            <Button type="primary" key="order" onClick={handleClick}>
              详情
            </Button>
          </div>
        ]}
        bodyStyle={{ padding: 0 }} // 移除 body 的内边距
        style={{ marginBottom: "5px" }}
      >
        <div style={{ paddingLeft: "1rem", paddingRight: "1rem", paddingBottom: 0 }}>
          <h3 style={{ fontSize: "1.25rem", fontWeight: 500, marginTop: 0 }}>
            {travelInfo.title}
          </h3>
        </div>
      </Card>
    </>
  );
}

export default function TravelCards({ travelList }) {
  return (
    <>
      <Row justify="center" style={{ width: "100%" }}>
        {travelList.map((item) => (
          <Col key={item.id} span={24}>
            <TravelCard travelInfo={item} />
          </Col>
        ))}
      </Row>
    </>
  );
}
